@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.3/font/bootstrap-icons.css");
body{
  background: rgb(20, 20, 34);
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #ccc;
  }
  .banner{
    position: relative;
  }

  .container{
    position: absolute;
    width: 80%;
    margin: 0 10%;
    height: 80vh;
    /* border:12px solid red; */
    overflow: hidden;
    background-image: url('https://wallpaper.dog/large/20502651.jpg');
    background-position: center;
    background-size: cover;
  }
  
  .portfolio {
    left: 0;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: url("https://prod-ne-cdn-media.puregym.com/media/814806/too-tired-to-workout.jpg?quality=80&width=992");
    background-position: center;
    background-size: cover;
  }
  
  .portfolio-logo {
    font-size: 3rem;
    color: #333;
    margin-bottom: 2rem;
  }
  
  .portfolio-description {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .portfolio-description h2 {
    font-size: 2rem;
    font-weight: 400;
    color: #333;
    margin-bottom: 1rem;
  }
  
  .portfolio-description span {
    font-weight: 700;
    color: #ff5722;
  }
  
  .portfolio-description p {
    font-size: 1.2rem;
    color: #666;
    line-height: 1.5;
    margin-bottom: 1rem;
  }
  
  .portfolio-social-icons {
    display: flex;
    justify-content: center;
  }
  
  .portfolio-social-icons a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ff5722;
    margin-right: 1rem;
    transition: transform 0.3s
  }  
  .portfolio-social-icons a:hover{
    transform: scale(1.1);
  }
  .container .logine-form{
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 58%);
    height: 100%;
    background: url('https://hips.hearstapps.com/hmg-prod/images/22i-worked-out-every-day-for-a-month-to-kickstart-my-fitness-routine-and-this-is-what-i-learnt-22-1671720737.jpg');
    background-position: center;
    background-size: cover;
  }
  .btnx{
    width: 100%;
    padding: 14px 20px;
    border: 0;
    outline: none;
    border-radius: 10px;
    transition: .5s ease;
    font-size: 18px;
    color: #000;
  }
  .btnx:hover{
    background: #000;
    color: #fff;
    transform: scale(0.9);
  }

  .form-box{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background: transparent;
    backdrop-filter: blur(20px);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    color: #e4e4e4;
  }

  .form-box h2{
    font-size: 32px;
    text-align: center;
  }

  .form-box .input-box{
    width: 340px;
    height: 50px;
    border-bottom: 2px solid #e4e4e4;
    margin: 30px 0;
  }
  .input-box input{
    width: 340px;
    height: 50px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 16px;
    color: #e4e4e4;
    font-weight: 500;
    padding-right: 28px;
  }
  .input-box label{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 16px;
    font-weight: 500;
    pointer-events: none;
    transition: .5s ease;
  }

.social-icons{
  margin-top: 90%;
  margin-bottom: 0;
  /* border: 3px solid red; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.social-icons a .bi{
  font-size: 40px;
  transition: .5s ease;
  /* margin-top: 30%; */
}
.social-icons a .bi:hover{
  transform: scale(1.9);
  color: #fff;
}


  header a,
  header button {
    font-size: 1rem;
    display: inline-block;
    background-color: #fff8e7;
    background: orangered;
    margin: 1rem;
    padding: 1rem;
    text-decoration: none;
    color: #000;
    cursor: pointer;
  }



